
.App {
  text-align: left;
}
.App_center {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #a8334a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.text-placement{
  background-color: #a8334a;
  display: flex;
  text-align: center;
}
.Box{
  padding: 15px;
  border-radius: 50px ;
  border: 2px solid skyblue;
  background-color: #a8334a;

}
th {
  text-align: center;
  background: #a8334a;
  position: sticky;
  position: -webkit-sticky;
  border: 1px solid black;
  top: 0;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
